import { Component } from "react";
import axios from "../axios";
import { AppBar, Toolbar, Typography, IconButton } from "@material-ui/core";
import MenuIcon from '@material-ui/icons/Menu';
import DayTab from "./DayTab";
import HomeFixtures from "./HomeFixtures";
import LoadingAnim from "./LoadingAnim";

class Home extends Component{

    constructor(props){
        super(props);

        let today = new Date().toISOString().slice(0, 10)

        this.state = {
            fixtures: [],
            currentDate: `${today}`,
            loaded: false,
        }

        this.getRequest = this.getRequest.bind(this);
        this.changeDate = this.changeDate.bind(this);
    }

     getRequest(date){
         this.setState({loaded: false})
         axios.get(`/fixtures/date/${date}`).then(({data}) => {
             this.setState({fixtures: data.api.fixtures, loaded: true})
         })
     }

     changeDate(date){
         this.getRequest(date);
     }

     componentDidMount(){
         this.getRequest(this.state.currentDate);
     }

    render(){

        return(
            <div className="home">

                <AppBar position="static" className="appbar">
                    <Toolbar>
                        <IconButton edge="start" color="inherit" aria-label="menu">
                        <MenuIcon />
                        </IconButton>
                        <Typography variant="h6">
                        Matches
                        </Typography>
                    </Toolbar>
                </AppBar>

                <DayTab 
                    changeDate={this.changeDate}
                />
                {/* Fixtures component will only load once get request has been sucessful */}
                {this.state.loaded ? 
                <HomeFixtures 
                    fixtures={this.state.fixtures}
                />
                :
                <LoadingAnim />
                }
            </div>
        )
    }
}

export default Home;
